<template>
  <div class="order-detail">
    <ul class="crumbs clearfix">
      <li>
        <router-link tag='div' to="/37duc/personCenter/main">
          个人中心>
        </router-link>
      </li>
      <li class="order">
        <router-link tag='div' to="/37duc/personCenter/main/myOrder">
          个人订单>
        </router-link>
      </li>
      <li style=" color: #1C1D1D;">
        <router-link tag='div' to="/37duc/personCenter/main/myOrder">
          个人订单详情
        </router-link>
      </li>
    </ul>
    <div class="order-info ">
      <p class="clearfix number-state">
        <span class="number">订单编号：{{orderDetail.sn}}</span>
        <span class="state">{{orderStatus(orderDetail.status)}}</span>
      </p>
      <div class="title-logo clearfix">
        <p class="company-title">{{orderDetail.name}}</p>
        <p class="logo"><img :src="baseSrc+bbCompanyInfo.logo" alt="公司logo"></p>
      </div>

      <div class="info-wrapper">
        <p class="info"><span class="tbr">投保人：{{tbrInfo.name}}</span><span>被保险人：{{bbxrInfo.name}}</span></p>
        <p class="info">保险期间：{{orderDetail.policy_effective_date}}至{{orderDetail.policy_expiration_date}}</p>
        <p class="info">保险公司：{{bbCompanyInfo.name}}</p>
        <p class="info" v-if="orderDetail.policy_num">保单号：{{orderDetail.policy_num}}</p>
        <p class="price">{{orderDetail.price}}<span>元</span></p>
      </div>
    </div>
    <div class=" bzfa-wrapper clearfix">
      <div class="layer bzfa-layer">
        <p class="title">
          保障方案
        </p>
        <p class="plan-name" v-if="bzfaInfo.plan_name">{{orderDetail.name}}——{{bzfaInfo.plan_name}}</p>
        <table border="1" class="bz-detail">
          <tr>
            <th>保障项目</th>
            <th>保障额度</th>
            <th>保障说明</th>
          </tr>
          <tr v-for="(item,index) in productInfo" :key="index">
            <td class="bzxm">{{item.name}}
            </td>
            <td class="bzed">{{item.ed}}</td>
            <td class="bzsm">{{item.value}}</td>
          </tr>
        </table>
      </div>

    </div>
    <div class="other-info clearfix">
      <div class="layer layer-ts" ref="layerTs1">
        <p class="title ">
          订单信息
        </p>
        <div class="info-wrapper">
          <div class="wrapper"
               v-if="orderDetail.status===1||orderDetail.status===2||orderDetail.status===3||orderDetail.status===4||orderDetail.status===5||orderDetail.status===6">
            <p class="info ">支付方式：{{payInfo.paymentPluginName}}</p>
            <p class="info">支付金额：¥{{payInfo.amount}}</p>
            <p class="info info-lsh">支付流水号：{{payInfo.transactionId}}</p>
            <p class="info">付款时间：{{payInfo.create_time}}</p>
          </div>
          <p class="info">订单编号：{{orderDetail.sn}}</p>
          <p class="info">创建时间：{{orderDetail.create_time}}</p>
        </div>
      </div>
      <div class="layer layer-ts" ref="layerTs2">
        <p class="title ">
          投保人信息
        </p>
        <div class="info-wrapper">
          <p class="info info-ts"><span>姓名：</span>{{tbrInfo.name}}</p>
          <p class="info info-ts"><span>性别：</span>{{getSexByName(tbrInfo.gender)}}</p>
          <p class="info info-ts"><span>身份证号：</span>{{tbrInfo.id_num}}</p>
          <p class="info info-ts"><span>出生日期：</span>{{dateFormat(tbrInfo.birth)}}</p>
          <p class="info info-ts"><span>联系电话：</span>{{tbrInfo.mobile}}</p>
        </div>
      </div>
      <div class="layer layer-ts " ref="layerTs3">
        <p class="title ">
          被保险人信息
        </p>
        <div class="info-wrapper ">
          <p class="info info-ts"><span>姓名：</span>{{bbxrInfo.name}}</p>
          <p class="info info-ts"><span>性别：</span>{{getSexByName(bbxrInfo.gender)}}</p>
          <p class="info info-ts"><span>身份证号：</span>{{bbxrInfo.id_num}}</p>
          <p class="info info-ts"><span>出生日期：</span>{{dateFormat(bbxrInfo.birth)}}</p>
          <p class="info info-ts"><span>是投保人的：</span>{{getRelation(bbxrInfo.type)}}</p>
        </div>
      </div>
      <div class="layer layer-ts" ref="layerTs4">
        <p class="title ">
          受益人信息
        </p>
        <div class="info-wrapper">
          <p class="info">受益人：{{orderDetail.beneficiary}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { orderStatus, getSexByName, dateFormat, getRelation } from '@/assets/js/mixins/base.js'

export default {
  mixins: [orderStatus, getSexByName, dateFormat, getRelation],
  data () {
    return {
      orderDetail: {},
      tbrInfo: {},
      bbxrInfo: {},
      bbCompanyInfo: {},
      bzfaInfo: {},
      payInfo: {},
      productInfo: {},
      baseSrc: ''
    }
  },
  mounted () {
    this.getOrderDetail()
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
    //
  },
  methods: {
    getOrderDetail () {
      let postData = {
        orderId: this.$route.query.orderId
      }
      this.$api.personCenter.orderDetail(postData).then(res => {
        if (res.code === 1) {
          this.orderDetail = res.data
          this.tbrInfo = res.data.member_info
          this.bbxrInfo = res.data.relation_info
          this.bbCompanyInfo = res.data.brand_info
          this.bzfaInfo = res.data.plan_info
          this.productInfo = res.data.product_info.parameter_values[0].entries
          for (let i = 0; i < this.productInfo.length; i++) {
            if (this.productInfo[i].name === res.data.plan_info.plan_details[i].name) {
              this.$set(this.productInfo[i], 'ed', res.data.plan_info.plan_details[i].value)
            }
          }
          this.payInfo = res.data.payment_info
          if (this.orderDetail.status === 1 || this.orderDetail.status === 2 || this.orderDetail.status === 3 || this.orderDetail.status === 4 || this.orderDetail.status === 5 || this.orderDetail.status === 6) {
            this.$refs.layerTs1.style.height = '246px'
            this.$refs.layerTs2.style.height = '246px'
            this.$refs.layerTs3.style.height = '246px'
            this.$refs.layerTs4.style.height = '246px'
          }
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";

  table, th, td {
    border: 1px solid #EDEDED;
    box-sizing: border-box;
    text-align: center;
  }

  .crumbs {
    height: 50px;
    line-height: 50px;;
    li {
      float: left;
      font-size: 14px;
      color: #A5A8A8;
      &:hover {
        color: #1C1D1D;
      }
    }
  }

  .order-info {
    background: #fff;
    padding: 0 0 9px 19px;
    .number-state {
      font-size: 14px;
      color: #B6BBBB;
      height: 52px;
      line-height: 52px;
      border-bottom: 1px solid #F1F4F4;
      box-sizing: border-box;
      .state {
        float: right;
        padding-right: 20px;
      }
    }
    .title-logo {
      margin-top: 20px;
      position: relative;
      .company-title {
        float: left;
        font-size: 20px;
        color: $color-text;
        font-weight: bold;
      }
      .logo {
        position: absolute;
        right: 20px;
        top: 0;
        width: 70px;
        height: 40px;
        img {
          width: 70px;
          height: 40px;
        }
      }
    }
  }

  .bzfa-wrapper {
    margin: 12px 0;

    .bzfa-layer {
      width: 100%;
      padding-bottom: 31px;
      width: calc(100% - 20px);
      .plan-name {
        margin-top: 14px;
      }
      table {
        margin-top: 12px;
        padding-left: 10px;
      }
    }
  }

  .layer {
    border-top: 2px solid #FF703F;
    float: left;
    background: #fff;
    margin-right: 12px;
    padding-left: 20px;
    width: 246px;
    &:last-child {
      margin-right: 0;
    }
    .title {
      font-size: 16px;
      color: #FF703F;
      height: 48px;
      line-height: 48px;
      box-sizing: border-box;
      border-bottom: 1px solid #F1F4F4;
    }
  }

  .layer-ts {
    height: 204px;
  }

  .info-wrapper {
    position: relative;
    padding-top: 20px;
    .info {
      font-size: 14px;
      color: $color-text;
      margin-bottom: 12px;
      span {
        display: inline-block;
        /*width: 70px;*/
      }
      .tbr {
        margin-right: 110px;
      }
    }
    .info-ts {
      span {
      }

    }
    .info-lsh {
      line-height: 24px;
      margin-top: -6px;
      margin-bottom: 6px;
    }
    .price {
      position: absolute;
      right: 20px;
      bottom: 0;
      font-size: 20px;
      color: $color-active-text;
      span {
        font-size: 12px;
        color: #D5D8D8;
      }
    }
  }

  .bzfa-wrapper .bz-detail {
    th {
      height: 40px;
      line-height: 40px;
      font-size: 12px;
      color: $color-text;
      background: #F6F6F6;
    }

    .bzxm, .bzed {
      font-size: 12px;
      color: $color-text;
    }
    .bzxm {
      width: 160px;
    }
    .bzed {
      width: 140px;
    }
    .bzsm {
      text-align: left;
      width: 736px;
      padding: 12px 20px;
      font-size: 12px;
      color: #A5A8A8;
      line-height: 18px;
    }

  }
</style>
